<template>
    <el-dialog 
    :close-on-click-modal = "showClose"
    :show-close = "showClose"
    title="Register" 
    :visible.sync="dialogFormVisible">
        <el-form :model="form">
            <el-form-item label="User Name" :label-width="formLabelWidth">
                <el-input 
                clearable
                v-model="form.username" 
                autocomplete="on">
                    <template slot="prepend"><i class="el-icon-user"></i></template>
                </el-input>
            </el-form-item>
            <el-form-item label="Password" :label-width="formLabelWidth" >
                <el-input 
                v-model="form.password" 
                autocomplete="on" 
                clearable
                show-password
                @blur="PasswordCheck"
                type="password"
                >
                    <template slot="prepend"><i class="el-icon-lock"></i></template>
                </el-input>
            </el-form-item>
            <el-form-item :label="passwordTip" :label-width="formLabelWidth" >
                <el-input 
                v-model="repassword" 
                clearable
                show-password
                @blur="PasswordCheck"
                autocomplete="on" 
                type="password">
                    <template slot="prepend"><i class="el-icon-lock"></i></template>
                </el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="close">取 消</el-button>
            <el-button type="primary" 
            @click="Register"
            :disabled="passwordCheck">注 册</el-button>
        </div>
    </el-dialog>
</template>

<script>
import {Register} from "../../../util/submit"
export default {
    props:{
        dialogFormVisible:{ type:Boolean, requires: true}
    },
    data(){
        return{
            showClose: false,
            form:{
                username:"",
                password:"",
                mail:"",
                
                phone:"",
            },
            repassword:"",
            passwordCheck: true,
            formLabelWidth: '130px',
            passwordTip:"Repeat Password"
        }
    },
    methods:{
        Register(){
            let data = this.form
            let load = Register(data)
            load.then(() =>{
                this.$store.commit("updateUserName", data.username)
                this.$router.push({ name: 'info'}).then(() => this.$message("Login Success")
                ).catch(err => (this.$message.error(err)))
            }).catch((err) =>{
                if (err.response) {
                    this.$message.error("Error")
                } else {
                    this.$message.error("Error")
                }
            })

        },
        close(){
            this.$emit("closeByChild", !this.dialogFormVisible)
        },
        PasswordCheck(){
            if( this.form.password === this.repassword) {
                this.passwordTip = "Equal Passwoed"
                this.passwordCheck = !this.PasswordCheck
                return
            }
            else {
                this.passwordTip = "Not the Same"
                this.passwordCheck = !this.PasswordCheck
                return
            }
        }
    }
}
</script>

<style scoped>
.el-dialog__footer {
    padding: 10px 20px 20px;
    text-align: center;
    box-sizing: border-box;
}
</style>>