<template>
    <el-main >
        <el-row>
            <el-col 
            :span="8" 
            :offset="8" 
            v-loading="logining"
            class="mainpart">
                <h1 class="welcome">Welcome to Glimmer</h1>
                <el-row>
                <el-input 
                placeholder="input username" 
                clearable
                v-model="usrname">
                    <template slot="prepend"><i class="el-icon-user"></i></template>
                </el-input>   
                </el-row>
                <el-row>
                <el-input 
                v-model="usrpassword" 
                clearable
                show-password
                type="password" 
                placeholder="input password">
                    <template slot="prepend"><i class="el-icon-lock"></i></template>
                </el-input>
                </el-row>
                <el-row>
                    <el-button @click="login">Submit</el-button>
                </el-row>
                <el-row>
                    <el-button @click="register" >Register</el-button>
                 </el-row>
            </el-col>
        </el-row>
        <RegisterDialog 
        v-on:closeByChild="closeByChild"
        :dialogFormVisible="dialogFormVisible"></RegisterDialog>
        
    </el-main>
</template>

<script>
import RegisterDialog from "./RegisterDialog"
import {myget} from "../../../util/submit"
// import { mapMutations } from 'vuex'
export default {
    components:{
        RegisterDialog,
    },
    data() {
        return {
            usrname: "",
            usrpassword: "",
            dialogFormVisible: false,
            logining : false
        }
    },
    methods:{
        // ...mapMutations([
        //     'updateUserName'
        // ]),
        login(){
            this.logining = !this.logining
            let query = "username="+this.usrname+"&password="+this.usrpassword
            let response = myget("GET", "users/login?" , query)// eslint-disable-line no-unused-vars
            response.then(()=>{
                this.logining = !this.logining
                this.$store.commit("updateUserName", this.usrname)
                this.$router.push({name:'info'}).catch(err =>(this.$message.error(err)))
            }  
            ).catch(()=>{    
                this.$message.error('Password or User Name is Error!OR You should Try to Register!');
                this.logining = !this.logining
                }
            )
        },
        register() {
            this.dialogFormVisible = true;
        },
        closeByChild(value){
            this.dialogFormVisible = value
        }
    },
    mounted(){

    }
}
</script>

<style scoped>
.mainpart{
    background-color:#ffffff;
    border-radius: 4px;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04)
}
.el-row{
    margin: 5px ; 
}
.el-button{
    width:100%
}
.el-main{
    margin-top:140px;
    vertical-align: center;
}
.welcome{
    text-align: center
}
</style>